html {
    font-size: 62.5%; // 10px --> 1rem
    scroll-behavior: smooth;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

ul, ol {
    list-style: none;
}

button {
    background: none;
    border: none;

    &:hover {
        cursor: pointer;
    }
}

a {
    cursor: pointer;
}

input {
    border: none;
    background: transparent;
}

h1 {@include fontSize($h1, 700, 1, $h1-mobile);}
h2 {@include fontSize($h2, 700, 1, $h2-mobile);}
p  {@include fontSize($p, 400, 2.4rem, $p-mobile);}